const ID_SIDE_BAR = "side-bar";
const ID_EXTRA_RIGHT = "extra-right";
const ID_HEADER = "ql-sw-header";
export function querySelectorIncludesText(selector, text, parent = document) {
    try {
        return Array.from(parent?.querySelectorAll?.(selector))?.find((el) => el?.textContent?.includes(text));
    }
    catch (error) {
        return null;
    }
}
export function createElementFromHTML(htmlString) {
    const div = document.createElement("div");
    div.innerHTML = htmlString.trim();
    return div.firstChild;
}
export function polling(callback, execute, maxRetry = 1000) {
    const interval = 100; // ms
    if (maxRetry <= 0) {
        return;
    }
    const id = setTimeout(function () {
        if (callback()) {
            execute();
            id && clearTimeout(id);
        }
        else {
            polling(callback, execute, maxRetry - 1);
            id && clearTimeout(id);
        }
    }, interval);
}
export class GroupApi {
    id;
    name;
    $el;
    apiList = [];
    href;
    swaggerUI;
    get $inner() {
        return this.$el.querySelector("div.opblock");
    }
    constructor(opts) {
        this.$el = opts.$el;
        this.swaggerUI = opts.swaggerUI;
        this.name = this.$el?.querySelector("h3")?.getAttribute("data-tag") ?? "";
        this.id = `group-${camelCase(this.name)}`;
        this.$el.id = this.id;
        this.apiList = Array.from(this.$el.querySelector("div.operation-tag-content")?.childNodes)?.map(($el) => new Api({ $el, parent: this }));
        this.href = this.$el.querySelector("h3 a")?.getAttribute("href") ?? "";
    }
}
export class Api {
    id;
    description;
    path;
    method;
    href;
    $el;
    parent;
    $btnExpand;
    swaggerUI;
    get $responsesInner() {
        return this.$el.querySelector("div.responses-inner");
    }
    get $responsesTableExample() {
        return this.$el.querySelector("table.responses-table[aria-live='polite']");
    }
    get $responsesTableLive() {
        return this.$el.querySelector("table.responses-table.live-responses-table");
    }
    get $codePreResponse() {
        return this.$responsesTableLive?.querySelector("pre code");
    }
    get $responseHeaders() {
        return querySelectorIncludesText("h5", "Response headers", this.$responsesTableLive)
            ?.parentElement;
    }
    get isExpanded() {
        return this.$btnExpand.getAttribute("aria-expanded") === "true";
    }
    constructor(opts) {
        this.$el = opts.$el;
        this.parent = opts.parent;
        this.swaggerUI = opts.parent.swaggerUI;
        this.method = this.$el?.querySelector(".opblock-summary-method")?.textContent ?? "";
        this.path = this.$el?.querySelector(".opblock-summary-path")?.getAttribute("data-path") ?? "";
        this.description = this.$el?.querySelector(".opblock-summary-description")?.textContent ?? "";
        this.href = this.$el?.querySelector(".opblock-summary-path a")?.getAttribute("href") ?? "";
        this.$btnExpand = this.$el.querySelector("button.opblock-control-arrow");
        this.id = `api-${camelCase(this.method)}-${camelCase(this.path)}`;
        this.$el.id = this.id;
        // if (this.parent?.$inner) {
        //   new MutationObserver((mutations) => {
        //     mutations.forEach((mutation) => {
        //       if (mutation.type === "childList") {
        //         polling(
        //           () => !!this.$responsesInner,
        //           () => {
        //           },
        //         )
        //       }
        //     })
        //   }).observe(this.parent.$inner, {
        //     childList: true,
        //   })
        // }
        this.handleHiddenResponseExample();
    }
    injectCopyToClipboardField() {
        const pre = this?.$responsesTableLive?.querySelector("pre");
        if (pre) {
            injectCopyToClipboardField(pre, this.swaggerUI);
        }
    }
    handleHiddenResponseExample() {
        const observer = new MutationObserver((mutations) => {
            mutations.forEach((mutation) => {
                if (mutation.type === "attributes") {
                    if (this.isExpanded) {
                        console.log("handleHiddenResponseExample");
                        this.handleChangeResponseLive();
                        polling(() => !!this.$responsesTableExample?.style, () => {
                            this.$responsesTableExample.style.display = "none";
                        });
                    }
                }
            });
        });
        observer.observe(this.$btnExpand, {
            attributes: true,
        });
    }
    hideResponseHeader() {
        if (this.$responseHeaders?.style) {
            this.$responseHeaders.style.display = "none";
        }
    }
    handleChangeResponseLive() {
        const observer = new MutationObserver((mutations) => {
            mutations.forEach((mutation) => {
                if (mutation.type === "childList") {
                    if (this.isExpanded) {
                        console.log("handleChangeResponseLive");
                        polling(() => !!this.$responseHeaders?.style, () => {
                            this.$responseHeaders.style.display = "none";
                        });
                        // polling(
                        //   () => !!this.$codePreResponse?.style,
                        //   () => {
                        //     this.injectCopyToClipboardField()
                        //     // this.handleChangeCodePre()
                        //   },
                        // )
                    }
                }
            });
        });
        if (this.$responsesInner) {
            // this.injectCopyToClipboardField()
            console.log("handleChangeResponseLive", "success");
            observer.observe(this.$responsesInner, {
                childList: true,
            });
        }
    }
    handleChangeCodePre() {
        const observer = new MutationObserver((mutations) => {
            mutations.forEach((mutation) => {
                if (mutation.type === "childList") {
                    if (this.isExpanded) {
                        console.log("handleChangeCodePre");
                        this.injectCopyToClipboardField();
                    }
                }
            });
        });
        if (this.$codePreResponse) {
            // observer.observe(this.$codePreResponse, {
            //   childList: true,
            // })
        }
    }
}
export class SwaggerUIX {
    logger = {
        error: (...args) => {
            console.error("[SWAGGER] [ERROR]", args);
        },
        info: (...args) => {
            console.info("[SWAGGER] [INFO]", ...args);
        },
    };
    mouseEvent = null;
    groupApiList = [];
    swaggerUIBundle;
    $sideBar = createElementFromHTML(`<div id="${ID_SIDE_BAR}" class="side-bar"></div>`);
    $extraRight = createElementFromHTML(`<div id="${ID_EXTRA_RIGHT}" class="${ID_EXTRA_RIGHT}"></div>`);
    $headerWrapper = createElementFromHTML(`<div id="${ID_HEADER}"></div>`);
    get $schemaContainer() {
        return document.querySelector("div.scheme-container");
    }
    get $topBar() {
        return document.querySelector("div.topbar");
    }
    get $informationContainerWrapper() {
        return document.querySelector(".information-container.wrapper");
    }
    get $mainWrapper() {
        return this.$sectionWrapper.parentElement;
    }
    get $schemesWrapper() {
        return document.querySelector("section.schemes.wrapper");
    }
    get $sectionWrapper() {
        return document.querySelector("div.wrapper > section.block.block-desktop");
    }
    get $models() {
        return document.querySelector("section.models");
    }
    storageType = "chromeStorage";
    // eslint-disable-next-line @typescript-eslint/no-empty-function, no-useless-constructor
    constructor(opts) {
        this.swaggerUIBundle = opts?.swaggerUIBundle;
        const { initOnPageLoaded = false, storageType = "chromeStorage" } = opts ?? {};
        this.storageType = storageType;
        if (initOnPageLoaded) {
            document.addEventListener("DOMContentLoaded", () => {
                this.initUI();
            });
        }
        this.trackMouse();
        this.handleResponseInterceptor();
    }
    initUI() {
        setTimeout(() => {
            this.onPageLoaded();
        }, 500);
    }
    handleResponseInterceptor() {
        this.onResponse((response) => {
            setTimeout(() => {
                this.injectCopyToClipboardField();
            }, 1000);
        });
    }
    onResponse(cb) {
        if (this.swaggerUIBundle) {
            this.swaggerUIBundle.getConfigs().responseInterceptor = (response) => {
                cb(response);
            };
        }
    }
    onPageLoaded() {
        this.hideUINotNeeded();
        const els = Array.from(this.$sectionWrapper?.firstChild?.childNodes);
        els?.forEach(($el) => {
            this.groupApiList.push(new GroupApi({ $el, swaggerUI: this }));
        });
        this.changeLayout();
        document.addEventListener("change", (e) => {
            this.groupApiList.forEach((groupApi) => {
                groupApi.apiList.forEach((api) => {
                    api?.hideResponseHeader();
                });
            });
        });
    }
    changeLayout() {
        this.$schemesWrapper.prepend(this.$headerWrapper);
        this.$mainWrapper.prepend(this.$sideBar);
        this.$mainWrapper.append(this.$extraRight);
        this.$extraRight.style.width = `45rem`;
        this.$mainWrapper.style.display = "flex";
        this.$mainWrapper.style.flexDirection = "row";
        this.$mainWrapper.style.maxWidth = `fit-content`;
        this.$mainWrapper.style.padding = `0px 30px`;
        this.$sideBar.style.width = `25rem`;
        this.$sideBar.style.marginRight = `3rem`;
        this.$sectionWrapper.style.width = `100rem`;
        this.$sectionWrapper.style.overflow = `auto`;
        this.$sectionWrapper.style.maxHeight = `60rem`;
        const SwaggerSideBar = withStorage(SwaggerSideBarComponent, { storageType: this.storageType });
        const SwaggerHeader = withStorage(SwaggerHeaderComponent, { storageType: this.storageType });
        const SwaggerExtraRightSection = withStorage(SwaggerExtraRightSectionComponent, {
            storageType: this.storageType,
        });
        UIManager.render({ Component: React.createElement(SwaggerSideBar, { swaggerUI: this }), id: ID_SIDE_BAR });
        UIManager.render({ Component: React.createElement(SwaggerHeader, { swaggerUI: this }), id: ID_HEADER });
        UIManager.render({
            Component: React.createElement(SwaggerExtraRightSection, { swaggerUI: this }),
            id: ID_EXTRA_RIGHT,
        });
    }
    hideUINotNeeded() {
        // hide top bar
        if (this.$topBar?.style) {
            this.$topBar.style.display = "none";
        }
        if (this.$informationContainerWrapper?.style) {
            this.$informationContainerWrapper.style.display = "none";
        }
        if (this.$schemaContainer?.style) {
            this.$schemaContainer.style.padding = `10px 0`;
        }
        if (this.$models?.style) {
            this.$models.style.display = "none";
        }
    }
    injectCopyToClipboard() {
        injectCopyToClipboard();
    }
    injectCopyToClipboardField() {
        this.groupApiList.forEach((g) => {
            g.apiList.forEach((api) => {
                api.injectCopyToClipboardField();
            });
        });
    }
    trackMouse() {
        const onMouseUpdate = (e) => {
            this.mouseEvent = e;
        };
        document.addEventListener("mousemove", onMouseUpdate, false);
        document.addEventListener("mouseenter", onMouseUpdate, false);
    }
    async login(email, password) {
        const callLogin = async (data) => {
            return new Promise((resolve) => {
                fetch(`${location.origin}/api/v1/auth/login`, {
                    headers: {
                        accept: "application/json, text/plain, */*",
                        "content-type": "application/json",
                    },
                    body: JSON.stringify(data),
                    method: "POST",
                    mode: "cors",
                })
                    .then((res) => res.json())
                    .then((data) => {
                    resolve(data);
                })
                    .catch((err) => {
                    this.logger.error(err);
                });
            });
        };
        function clickAuthBtn() {
            const authButton = document.querySelector(".auth-btn-wrapper .modal-btn.auth");
            if (authButton) {
                authButton?.click();
            }
        }
        ;
        (async () => {
            const payload = {
                provider: "email",
                email: email ?? config.cr.username,
                password: password ?? config.cr.password,
            };
            const res = (await callLogin(payload));
            this.logger.info(`${res?.data?.accessToken?.token}`);
            const jwtToken = res.data.accessToken.token;
            setTimeout(function () {
                const openAuthFormLockButton = document.querySelector(".auth-wrapper .authorize.locked");
                if (openAuthFormLockButton) {
                    openAuthFormLockButton?.click();
                    clickAuthBtn();
                }
                else {
                    const openAuthFormUnlockButton = document.querySelector(".auth-wrapper .authorize.unlocked");
                    openAuthFormUnlockButton?.click();
                }
                const tokenInput = document.querySelector(".auth-container input");
                const closeButton = document.querySelector("button.btn-done");
                const nativeInputValueSetter = Object.getOwnPropertyDescriptor(window?.HTMLInputElement?.prototype, "value").set;
                nativeInputValueSetter.call(tokenInput, jwtToken);
                const inputEvent = new Event("input", { bubbles: true });
                tokenInput.dispatchEvent(inputEvent);
                clickAuthBtn();
                closeButton.click();
            }, 400);
        })();
    }
}
